@font-face {
    font-family: Montserrat;
    src: url(./static/Montserrat-Regular.ttf) format('truetype');
    font-weight: normal;
}
@font-face {
    font-family: Montserrat;
    src: url(./static/Montserrat-Bold.ttf) format('truetype');
    font-weight: bold;
}
@font-face {
    font-family: Montserrat;
    src: url(./static/Montserrat-Medium.ttf) format('truetype');
    font-weight: 600;
}
@font-face {
    font-family: Montserrat;
    src: url(./static/Montserrat-Light.ttf) format('truetype');
    font-weight: 300;
}
@font-face {
    font-family: Open-sans;
    src: url(./static/Montserrat-Light.ttf) format('truetype');
}
@-webkit-keyframes pulsate-ripple {
    0% {
        -webkit-box-shadow: 0 0 0 0 rgba(13, 71, 161, 0.4);
                box-shadow: 0 0 0 0 rgba(13, 71, 161, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 100px rgba(204, 169, 44, 0);
                box-shadow: 0 0 0 100px rgba(204, 169, 44, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
                box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
    }
}
@keyframes pulsate-ripple {
    0% {
        -webkit-box-shadow: 0 0 0 0 rgba(13, 71, 161, 0.4);
                box-shadow: 0 0 0 0 rgba(13, 71, 161, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 100px rgba(204, 169, 44, 0);
                box-shadow: 0 0 0 100px rgba(204, 169, 44, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
                box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
    }
}
body {
    margin: 0;
    padding: 0;
    background-color: #fafafa;
    font-family: Open-sans, sans-serif;
    -webkit-font-smoothing: antialiased;
}



